html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

img[data-object-fit="contain"] {
  object-fit: contain;
}

img[data-object-fit="cover"] {
  object-fit: cover;
}

input,
select,
textarea {
  border-radius: 0;
}
