.page-header {
  position: absolute;
  z-index: 5;
  display: flex;
  justify-content: space-between;

  padding: 33px calc(50% - 705px);
  width: 100%;

  @media (max-width: 1500px) {
    padding: 33px 25px;
  }

  @media (max-width: $desktop-width) {
    padding: 20px 26px;
  }

  @media (max-width: $tablet-width) {
    padding: 16px 17px;
  }
}

.page-header__logo {
  display: flex;
  line-height: 0;
}

.page-header__logo-main {
  @media (max-width: $tablet-width) {
    width: 38px;
    height: 38px;
  }
}

.page-header__logo-container {
  display: flex;
  flex-direction: column;

  margin-top: 10px;
  margin-left: 15px;

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.page-header__logo-title {
  margin-bottom: 10px;
}
