.about {
  padding: 0 calc(50% - 585px);
  margin-bottom: 110px;
  width: 100%;

  @media (max-width: 1200px) {
    padding: 0 25px;
  }

  @media (max-width: $desktop-width) {
    padding-right: 20px;
    margin-bottom: 84px;
  }

  @media (max-width: $tablet-width) {
    padding: 0 16px;
    margin-bottom: 64px;
  }
}

.about__info {
  position: relative;
  display: flex;
  flex-direction: column;

  margin-left: 177px;
  width: 83%;

  @media (max-width: $desktop-width) {
    margin-left: 185px;
    width: 75%;
  }

  @media (max-width: $tablet-width) {
    margin-left: 0;
    width: 100%;
  }
}

.about__info img {
  content: "";
  position: absolute;
  left: -200px;
  top: 4px;

  width: 139px;
  height: 317px;

  background-image: url("../img/2005@1x.png");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;

  object-fit: cover;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/2005@2x.png");
  }

  @media (max-width: $desktop-width) {
    top: 0;
    left: -210px;

    width: 159px;
    height: 362px;
  }

  @media (max-width: $tablet-width) {
    display: none;
  }
}

.about__info h2 {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;

  font-size: 36px;
  line-height: 51px;
  font-weight: 400;
  color: $grey;

  @media (max-width: $desktop-width) {
    margin-bottom: 26px;
    font-size: 32px;
    line-height: 45px;
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 22px;
    font-size: 28px;
    line-height: 39px;
  }
}

.about__info p {
  margin: 0;
  margin-bottom: 20px;
  padding: 0;

  font-size: 16px;
  line-height: 22px;
  font-family: $ptmono;
  color: $grey;

  @media (max-width: $tablet-width) {
    margin-bottom: 38px;
  }

  @media (min-width: 768px) and (max-width: $desktop-width) {
    padding-right: 8px;
  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

.about__text-end {
  &--full {
    @media (max-width: $desktop-width) {
      display: none;
    }
  }

  &--short {
    display: none;

    @media (max-width: $desktop-width) {
      display: inline;
    }
  }
}

.about__info a {
  margin-top: 40px;
  padding-top: 14px;
  width: 180px;
  min-height: 45px;

  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  color: $blue;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  text-decoration: none;
  text-align: center;

  border: 1px solid $blue;
  box-sizing: border-box;
  border-radius: 5px;

  transition: 0.2s;

  @media (max-width: $tablet-width) {
    margin-top: 30px;
  }

  &:hover,
  &:focus {
    color: $white;
    background-color: $blue;
  }

  &:focus {
    outline: 0;
    border: 1px solid $blue-dark;
  }

  &:active {
    color: $blue;
    background-color: $white;
  }
}
